<template>
	<view class="addCompany">
		<view class="companyContent">
			<u--form :model="form" ref="uForm" labelWidth="160rpx">
				<u-form-item label="公司名称:" prop="name">
					<u-input v-model="form.name" />
				</u-form-item>
			</u--form>
			<view class="submitBtn">
				<u-button type="primary" @click="submit">提交</u-button>
			</view>
		</view>

	</view>
</template>

<script>
	export default {
		data() {
			return {
				form: {
					name: '',
				},
				rules: {
					name: [{
						required: true,
						message: '请输入姓名',
						trigger: ['blur', 'change']
					}]
				}
			};
		},
		methods: {
			submit() {
				this.$refs.uForm.validate().then(res => {
					// this.request()
					uni.$u.toast('校验通过')
				}).catch(errors => {
					uni.$u.toast('校验失败')
				})
			}
		},
	};
</script>
<style lang="scss" scoped>
	.addCompany {
		background: #fff;
		display: flex;
		justify-content: center;
		margin: 20rpx;
		border-radius: 16rpx;

		.companyContent {
			display: flex;
			flex-direction: column;
			justify-content: center;
			align-items: center;
			width: 100%;
			height: 500rpx;
			.submitBtn{
				width: 80%;
				margin-top: 40rpx;
			}
		}
	}
</style>